<template>
  <div>
    <Header></Header>

    <b-container class="mt-4">
      <b-card>
        <template #header>
          <Chevron v-b-toggle.b-card-1></Chevron>
          
        </template>
        <b-collapse id="b-card-1" visible>
          <b-row class="col-12">
            <div>
              <b-form-textarea
                class="mr-2"
                placeholder="Tall textarea"
                rows="15"
                v-model="area"
              ></b-form-textarea>
            </div>
            <!-- <span>&nbsp; : </span>
            <b-form-checkbox-group
              class="chWidth"
              v-model="nbaSelected"
              :options="nbaOptions"
              text-field="text"
              value-field="value"
              disabled-field="disabled"
              plain
            ></b-form-checkbox-group> -->
          </b-row>
          <b-row class="mt-2">
            <b-col><b-button @click="test456()">OK</b-button></b-col>
          </b-row>
        </b-collapse>
      </b-card>
    </b-container>

    <b-container class="mt-4">
      <b-card>
        <template #header>
          <Chevron v-b-toggle.b-card-2></Chevron>
          
        </template>
        <b-collapse id="b-card-2" visible>
          <b-table :items="gameArray" :fields="fields">
            <template #cell(spreads)="row">
              {{
                row.item.spreads > 0 ? "+" + row.item.spreads : row.item.spreads
              }}
            </template>
          </b-table>
        </b-collapse>
      </b-card>
    </b-container>

    <Footer></Footer>
  </div>
</template>


<script>
import Header from "@/components/Header.vue";
import Footer from "@/components/Footer.vue";
import Chevron from "@/components/Chevron.vue";

export default {
  components: {
    Header,
    Footer,
    Chevron,
  },
  // mounted() {
  //   this.getNbaOdds();
  // },
  data() {
    return {
      area: "",
      nbaSelected: [],
      nbaOptions: [
        { text: "Orange", value: "orange" },
        { text: "Apple", value: "apple" },
        { text: "Pineapple", value: "pineapple" },
        { text: "Grape", value: "grape" },
      ],
      // items: [],
      fields: [
        // { key: "sequence" },
        // { key: "mtid" },
        // { key: "boid" },
        // { key: "paid" },
        // { key: "lineid" }, //  < 
        { key: "eid" }, // ID
        // { key: "partid" }, // ID ID
        // { key: "adj" }, // 
        // { key: "pri" }, // 
        { key: "name", label: "", class: "text-center" },
        { key: "spreads", label: "", class: "text-center" },
        { key: "spreadsOdds", label: "", class: "text-center" },
        { key: "totals", label: "", class: "text-center" },
        { key: "totalsOdds", label: "", class: "text-center" },
      ],
      mappingPartid: [
        { partid: 1143, name: "" },
        { partid: 1144, name: "" },
        { partid: 1145, name: "" },
        { partid: 1146, name: "" },
        { partid: 1148, name: "" },
        { partid: 1149, name: "" },
        { partid: 1150, name: "" },
        { partid: 1151, name: "" },
        { partid: 1152, name: "" },
        { partid: 1153, name: "" },
        { partid: 1155, name: "" },
        { partid: 1156, name: "" },
        { partid: 1157, name: "" },
        { partid: 1158, name: "" },
        { partid: 1159, name: "" },
        { partid: 1161, name: "" },
        { partid: 1162, name: "" },
        { partid: 1164, name: "" },
        { partid: 1165, name: "" },
        { partid: 1166, name: "" },
        { partid: 1167, name: "" },
        { partid: 1168, name: "" },
        { partid: 1169, name: "" },
        { partid: 1160, name: "" },
        { partid: 1170, name: "" },
        { partid: 1171, name: "" },
        { partid: 1172, name: "76" },
        { partid: 15143, name: "" },
        { partid: 15144, name: "" },
      ],
      gameArray: [],
      spreadsArray: [],
      totalsArray: [],
    };
  },
  methods: {
    getNbaOdds(apiUrl) {
      this.axios
        .get(apiUrl)
        .then((response) => {
          this.gameArray = response.data.data.openingLines;
          this.gameArray = this.gameArray.filter((e) => e.partid < 9999);
          this.gameArray = this.gameArray
            .sort(this.compareLineid)
            .sort(this.compareEid);
          for (const [index, element] of this.gameArray.entries()) {
            element.name = this.findName(element);
            element.guestHost = index % 2 === 0 ? "guest" : "host";
            element._rowVariant = index % 4 < 2 ? "danger" : "success";
          }
          this.spreadsArray = response.data.data.bestLines.filter(
            (e) => e.partid < 9999
          );
          this.totalsArray = response.data.data.bestLines.filter(
            (e) => e.partid > 9999
          );
          for (const item of this.gameArray) {
            item.spreads = this.findObjInArray(
              this.spreadsArray,
              "partid",
              item.partid
            ).adj;
            item.spreadsOdds = this.findObjInArray(
              this.spreadsArray,
              "partid",
              item.partid
            ).pri;
            const big = this.findBigSmall(this.totalsArray, 15143, item.eid);
            const small = this.findBigSmall(this.totalsArray, 15144, item.eid);
            if (item.guestHost === "guest") {
              item.totals = this.checkAdj(big);
              item.totalsOdds = this.checkPri(big);
            } else {
              item.totals = this.checkAdj(small);
              item.totalsOdds = this.checkPri(small);
            }
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    compareEid(a, b) {
      return a.eid - b.eid;
    },
    compareLineid(a, b) {
      return a.lineid - b.lineid;
    },
    findName(item) {
      let str = "";
      for (const team of this.mappingPartid) {
        if (item.partid === team.partid) {
          str = team.name;
        }
      }
      return str;
    },
    findObjInArray(array, propName, propValue) {
      return array.find((e) => e[propName] === propValue);
    },
    findBigSmall(array, partid, eid) {
      const temp = array.filter((e) => e.partid === partid);
      if (temp.find((e) => e.eid === eid) !== null) {
        return temp.find((e) => e.eid === eid);
      } else {
        return { adj: "", pri: "" };
      }
    },
    process(value) {
      if (value > 0) {
        return "+" + value;
      }
    },
    test456() {
      this.getNbaOdds(this.area);
    },
    checkAdj(item) {
      if (item !== undefined) {
        return item.adj;
      } else {
        return "";
      }
    },
    checkPri(item) {
      if (item !== undefined) {
        return item.pri;
      } else {
        return "";
      }
    },
  },
};
</script>